<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">查处环节分析</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="col-9">
          <div class="chart" id="chart6"></div>
        </div>
        <div class="col-3">
          <table class="table tablestyle table-striped">
            <tbody>
              <tr>
                <td>36%</td>
                <td>￥4,214</td>
              </tr>
              <tr>
                <td>20%</td>
                <td>￥3531</td>
              </tr>
              <tr>
                <td>19%</td>
                <td>￥2689</td>
              </tr>
              <tr>
                <td>12%</td>
                <td>￥2111</td>
              </tr>
              <tr>
                <td>11%</td>
                <td>￥1668</td>
              </tr>
              <tr>
                <td>7%</td>
                <td>￥123</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import watchDate from '@/mixins/watchDate'
export default {
  props: ['dateType'],
  data () {
    return {
      loading: false
    }
  },
  mixins: [
    watchDate
  ],
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_TYPE_ARTICLES',
        params: {
          dateType: this.dateType
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let d = this.dataFormat(a)
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: d.title
        },
        series: [{
          name: '数量',
          type: 'pie',
          radius: ['50%', '70%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: d.data
        }]
      }
      let myChart = this.$echarts.init(document.getElementById('chart6'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        title: [],
        data: []
      }
      a.map(o => {
        obj.title.push(o.name)
        obj.data.push(o)
      })
      return obj
    }
  }
}

</script>



